import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Axios from '@/common/request.js'
import ApiConfig from '@/apiConfig.js'
import {Cell, CellGroup, Toast, Popup, List, Lazyload} from 'vant'
import * as utils from '@/common/utils.js'
import errorImg from '@/assets/images/500.png'

Vue.use(Cell).use(CellGroup).use(Toast).use(Popup).use(List).use(Lazyload, {
  preLoad: 1.3,
  error: errorImg,
  // loading: 'dist/loading.gif',
  attempt: 1
})
Vue.use(ApiConfig)

router.beforeEach((to, from, next) => {
  if (!!to.meta.pageTitle && typeof to.meta.pageTitle !== undefined) {
    utils.setPageTitle(to.meta.pageTitle)
  }
  next()
})
Vue.prototype.axios = Axios
Vue.prototype.ApiConfig = ApiConfig
Vue.config.productionTip = false
document.addEventListener('touchstart', () => {
}, false)
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
