<template>
  <div class="page app-download">
    <div class="content">
      <img src="../assets/images/50.png" class="qrcode" v-lazy="data.image" alt="下载链接">
      <p class="mt15">扫一扫下载星河WORLD APP</p>
      <template v-if="!fromApp">
        <a v-if="isApple" @click="checkWX(data.ios_url)" class="ios"><span class="icon appicon"></span>iOS下载</a>
        <a v-else-if="isAndroid" @click="checkWX(data.android_url)" class="android"><span class="icon andicon"></span>安卓下载</a>
        <template v-else>
          <a @click="checkWX(data.ios_url)" class="ios"><span class="icon appicon"></span>iOS下载</a>
          <a @click="checkWX(data.android_url)" class="android"><span class="icon andicon"></span>安卓下载</a>
        </template>
      </template>
    </div>
    <div class="cpright">星河WORLD | 更美好的园区生活</div>
    <div @click="innerWXopen=false">
      <WxOverlay v-show="innerWXopen"/>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'
  import WxOverlay from '@/components/WxOverlay.vue'
  import {wxShare} from "@/common/wxShare";
  var isAndroid = utils.isAndroid()
  var isApple = utils.isApple()
  var fromApp = utils.fromApp()
  export default {
    name: 'about',
    data() {
      return {
        isAndroid,
        isApple,
        fromApp,
        innerWXopen: false,
        data: {}
      }
    },
    created() {
      var zone_id = utils.getQueryString(window.location.href, 'id')
      if (zone_id !== null) {
        this.axios.get(this.apiConfig.appDownUrl, {
          params: {zone_id}
        }).then(res => {
          if (res.s !== 1) {
            this.$toast.fail(res.m)
            return
          }
          this.data = res.d
          let title = '分享一个应用：星河WORLD 更美好的园区生活';
          let desc = '这个应用很不错，给你推荐一下,下载地址：' + res.d.web_url
          wxShare({
            title,
            desc,
            link: res.d.web_url,
            imgUrl: res.d.image,
            success: function () {
            }
          })
        })
      } else {
        setTimeout(() => {
          this.$router.push({
            name: 'Index'
          })
        }, 1000)
      }
    },
    components: {
      WxOverlay
    },
    methods: {
      checkWX(url) {
        if (utils.isWeixin()) {
          this.innerWXopen = true
        } else if (url) {
          window.location.href = url
        }
      }
    }
  }
</script>

<style scoped lang="scss">
@import '../assets/scss/Common/variables.scss';
.cpright {
  padding-bottom: rem(60px);
  color: rgb(75, 207, 150);
  text-align: center;
  font-size: rem(15px);
}
.app-download {
  background: url('../assets/images/bg.jpg') #fff no-repeat;
  background-size: 100% auto;
  text-align: center;
  img.qrcode {
    width: rem(169px);
    height: rem(169px);
    margin-top: rem(120px);
    box-shadow: 1px 1px 14px #c8f4df;
  }
}

.android, .ios {
  display: block;
  border: 2px solid rgb(75, 207, 150);
  height: rem(48px);
  border-radius: rem(24px);
  line-height: rem(48px);
  font-size: rem(16px);
  width: rem(210px);
  margin: rem(42px) auto 20px;
  text-decoration: none;
  color: rgb(75, 207, 150);
}
.icon {
  background-image: url('../assets/images/ios-icon.png');
  background-size: 83px auto;
  background-repeat: no-repeat;
  width: 26px;
  height: 20px;
  display: inline-block;
}
.appicon {
  position: relative;
  top: 3px;
}
.andicon {
  background-position: 0 -45px;
  position: relative;
  top: 4px;
}
</style>
