<template>
  <div class="page">
    <div class="content">
      <div class="p15 bgc-white" v-if="fetchData.advert_content">
        <p class="mt10" v-html="fetchData.advert_content"></p>
      </div>
    </div>
    <div class="btn-wrap box" v-if="fetchData.link_url">
      <a :href="fetchData.link_url" class="btn btn-success btn-round">马上体验</a>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'
  import {wxShare} from "@/common/wxShare"
  export default {
    name: 'banner',
    data() {
      return {
        fetchData: {}
      }
    },
    created() {
      var advert_id = utils.getQueryString(window.location.href, 'id')
      if (advert_id !== null) {
        this.axios.post(this.apiConfig.banner, {
          advert_id
        }).then(res => {
          if (res.s !== 1) {
            this.$toast.fail(res.m)
            return
          }
          this.fetchData = res.d
          let desc = '不能错过的重要信息，点击查看详情'
          wxShare({
            title: res.d.advert_title,
            desc,
            link: window.location.href,
            imgUrl: res.d.image_url,
            success: function () {
            }
          })
        })
      } else {
        setTimeout(() => {
          this.$router.push({
            name: 'Index'
          })
        }, 1000)
      }
    }
  }
</script>
<style>
  .btn-wrap {
    padding: rem(11px) rem(24px);
  }
</style>
