import Vue from 'vue'
import Axios from 'axios'
import * as utils from '@/common/utils.js'
import '@/common/md5.js'
import { Toast } from 'vant'

Vue.use(Toast)

let token = 'cdef48e68ea725274c60a18a030ae23a02712ce3' || window.sessionStorage.token || utils.getCookie('galaxy_token')
if (token) {
  window.sessionStorage.token = token
  Axios.defaults.headers.token = token
}

Axios.interceptors.request.use(config => {
    if (config.data==undefined) {
      config.data = {}
    }
    if(config.method == 'post') {
      config.data.sign = undefined;   // 避免错误时sign会保留
      config.data.timeout = +new Date();

      //定义数组，字符串
      var arr = [];
      var str = '';
      for(var i in config.data) {
        arr.push(i);
      }


      doStr(config.data);

      function  doStr(arr) {
          var newArr = [];
          for(var a in arr) {
            newArr.push(a);
          }

          if(str == '') {   // 第一层需要排序，一维数组
            newArr= newArr.sort();  //排序
          }

        newArr.forEach((i, k) => {
            if(typeof(arr[i]) != "undefined" &&  arr[i] != null) {  // 判断值不等于undefined。null
              if(Array.isArray(arr[i])) {    // 如果是数组，再拆分。es5判断数组方法
                arr[i].forEach((a,b) => {
                  doStr(a)
                })
              } else {
                str += i + "=" + arr[i] + '&';
              }
            }
          })
      }

      str += 'key=galaxyworld_html';
      console.log(str);

      config.data.sign= md5(str).toUpperCase();
    }
    return config
}, error => {
    Promise.reject(error)
})

Axios.interceptors.response.use((response) => {
  if (response.data && response.data.s != 1) {
      Toast({
        message:response.data.m,
        duration: 1500

      });
      return response.data
    }

    return response.data
  }, (error) => {
    alert(error.toString())
    return Promise.reject(error)
})

  export default Axios
