<template>
  <div class="page" id="prize">
    <div class="content">
      <div class="prize-wrap">
        <div class="tc title">
          <img src="../assets/images/company.png" width="130" alt="">
          <img src="../assets/images/intro.png" class="mt20" alt="">
        </div>
        <div class="pr">
          <div class="turn" id="turn">
            <div class="rotate1 gift" :class="'rotate' + (index + 1)" :key="v.ag_id"
                 v-for="(v, index) in prizeInfo.data">
              <div class="good-desc">{{ v.ag_name }}</div>
              <img :src="getGoods(v.ag_id)" alt="">
            </div>
          </div>
          <div class="notStartBtn" v-if="activityStatus == 0"></div>
          <a class="startBtn" @click="startRotate" v-if="activityStatus == 1"></a>
          <div class="activityEnd" v-if="activityStatus == 2"></div>
          <div v-if="fromApp && myPrize.list != 0 && activityStatus > 0" class="checkPrize" @click="openMyPrize">我的<br>中奖
          </div>
        </div>
        <div v-if="fromApp" class="tips">您还有&nbsp;<span class="white">{{ prizeInfo.activities_number }}</span>&nbsp;次抽奖机会
        </div>
        <div class="rules">
          <h3><span class="mr5">-</span>活动规则<span class="ml5">-</span></h3>
          <p>1、参与条件：11月17日后注册并完成认证的用户；</p>
          <p>2、活动时间：11月17日-12月9日；</p>
          <p>3、活动期间，每人有3次抽奖机会，每日1次；</p>
          <p>4、奖品领取时间及地点将以短信形式通知，请留意通知短信；</p>
          <p>5、礼品图片仅供参考，具体请以实物为准；</p>
          <p>6、活动解释权归星河WORLD APP运营方所有。</p>
        </div>
      </div>
    </div>
    <div @click="wxOverlay = false">
      <WxOverlay v-if="wxOverlay"/>
    </div>
    <div class="btn-wrap box" v-if="!fromApp">
      <a :href="prizeInfo.advert_url" @click="wakeApp" class="btn btn-app btn-round bf1">打开APP，参与抽奖</a>
    </div>
    <van-popup v-model="myPrize.show" :overlay="true" transition="pop">
      <div class="myprize-list">
        <span class="close" @click="myPrize.show=false"></span>
        <p class="title">我的中奖</p>
        <div class="box pt10 pb10 px-b" v-for="(item, k) in myPrize.list" :key="k">
          <img :src="item.ah_goods_image" alt="">
          <div class="bf1 ml10">
            <h5>{{ item.ah_name }}</h5>
            <p class="f12">请在2018-12-10 18:00前兑换，兑换细则请查看活动规则</p>
            <p class="f12 tr" :class='{"red": item.ah_used == 0, "gray": item.ah_used != 0}'>
              {{myPrize.status[item.ah_used]}}</p>
          </div>
        </div>
      </div>
    </van-popup>
    <van-popup v-model="noPrize" :overlay="true" transition="pop">
      <div class="noprize">
        <span class="close" @click="noPrize = false"></span>
        <p class="mb15">很遗憾，未中奖</p>
        <img src="../assets/images/goods_6.png" width="50" alt="">
      </div>
    </van-popup>
    <van-popup v-model="winingPrize.show" :overlay="true" class="pop2" transition="pop">
      <span class="close" @click="winingPrize.show = false"></span>
      <h3 class="title">中奖啦</h3>
      <div class="myprize-list tc">
        <img :src="winingPrize.prizeImg" alt="">
        <div class="mt15">{{ winingPrize.prizeName }}</div>
        <div class="myprize" @click="winingPrize.show = false;openMyPrize()">我的中奖</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
  import {wxShare} from "@/common/wxShare"
  import * as utils from '@/common/utils.js'
  import WxOverlay from '@/components/WxOverlay.vue'
  import goods_1 from '../assets/images/goods_1.png'
  import goods_2 from '../assets/images/goods_2.png'
  import goods_3 from '../assets/images/goods_3.png'
  import goods_4 from '../assets/images/goods_4.png'
  import goods_5 from '../assets/images/goods_5.png'
  import goods_6 from '../assets/images/goods_6.png'
  export default {
    name: 'about',
    data() {
      return {
        zone_id: null,
        fromApp: true,
        fromWX: false,
        wxOverlay: false,
        prizeInfo: {},
        activityStatus: 1,
        goods_1,
        goods_2,
        goods_3,
        goods_4,
        goods_5,
        goods_6,
        winingPrize: {
          show: false,
          prizeImg: '../assets/images/50.png',
          prizeName: ''
        },
        noPrize: false,
        myPrize: {
          show: false,
          list: [],
          status: ['未领取', '已领取', '已过期']
        }
      }
    },
    created() {
      this.init()
    },
    components: {
      WxOverlay
    },
    methods: {
      init() {
        this.zone_id = utils.getQueryString(window.location.href, 'id')
        this.fromApp = utils.fromApp()
        this.fromWX = utils.isWeixin()
        this.getPrizeInfo()
        this.openMyPrize()
      },
      getGoods(index) {
        return this['goods_' + index]
      },
      wakeApp() {
        if (utils.isWeixin()) {
          this.wxOverlay = true
          return
        }
        utils.wakeApp(this.apiConfig.downloadUrl.android, this.apiConfig.downloadUrl.ios)
      },
      getPrizeInfo() {
        this.axios.post(this.apiConfig.prizeInfo, {
          zone_id: this.zone_id
        }).then((res) => {
          if (res.s !== 1) {
            this.$toast(res.m)
            return
          }
          this.prizeInfo = res.d
          let title = '星粉幸运大转盘，身份认证就能抽奖哟'
          let desc = '点击查看详情'
          wxShare({
            title,
            desc,
            link: window.location.href,
            success: function () {
              // alert('分享成功1')
            }
          })
          var today = +new Date / 1000
          if (today < this.prizeInfo.aa_start_time) {
            this.activityStatus = 0
          } else if (today > this.prizeInfo.aa_end_time) {
            this.activityStatus = 2
          }
          this.prizeInfo.data.sort(() => {
            return Math.random() - 0.5
          })
          this.prizeInfo.data.forEach((v, k) => {
            v.angle = k * 60
          })
        })
      },
      openMyPrize(show = false) {
        if (this.activityStatus < 1 || !this.fromApp) {
          return
        }
        this.axios.post(this.apiConfig.myPrize, {
          zone_id: this.zone_id
        }).then((res) => {
          if (res.s !== 1) {
            this.$toast(res.m)
            return
          }
          if (show) {
            this.myPrize.show = true
          }
          res.d.forEach((v) => {
            v.ah_goods_image = this.getGoods(v.ah_goods_id)
          })
          this.myPrize.list = res.d
        })
      },
      startRotate() {
        if (this.fromWX) {
          this.wxOverlay = true
          return
        }
        if (!this.fromApp) {
          this.$toast('请在APP内参与活动')
          return
        }
        this.axios.post(this.apiConfig.prizeSubmit, {
          zone_id: this.zone_id
        }).then((res) => {
          if (res.s !== 1) {
            this.$toast(res.m)
            return
          }
          this.prizeInfo.activities_number--
          var id = res.d.ag_id
          var turn = document.getElementById('turn')
          var angle = this.prizeInfo.data.find((obj) => obj.ag_id === id).angle
          turn.style.cssText = 'transition: transform cubic-bezier(.39,1.03,.73,1) 8s;transform: rotate(' + (-angle + 360 * 5) + 'deg);'
          turn.addEventListener('transitionend', () => {
            if (id > 5) {
              this.noPrize = true
            } else {
              var prizeName = this.prizeInfo.data.find((obj) => obj.ag_id === id).ag_name
              var prizeImg = this.getGoods(id)
              Object.assign(this.winingPrize, {prizeName, prizeImg})
              this.winingPrize.show = true
              this.openMyPrize()
            }
          })
        })
      }
    }
  }

</script>
<style lang="scss">
  @import '../assets/scss/Common/variables.scss';

  .btn-wrap {
    padding: rem(11px) rem(24px);
  }
  .van-modal {
    position: absolute;
  }

  .noprize {
    text-align: center;
    width: 60vw;
    padding: rem(32px) 0 rem(15px);
  }

  .px-b:last-child {
    background: none;
  }

  .pop2 {
    background-color: transparent !important;

    .close {
      right: rem(14px);
      top: rem(30px);
    }

    .title {
      position: relative;
      width: rem(100px);
      background-color: #e2a941;
      padding: rem(9px) rem(12px);
      border-radius: 4px;
      color: #fff;
      margin: 0 auto;
      z-index: 3;
      text-align: center;
    }
  }

  .close {
    position: absolute;
    right: rem(10px);
    top: rem(10px);
    z-index: 3;
    cursor: pointer;
  }

  #prize {
    background-color: #4bcf96;
    .van-popup {
      border-radius: 8px;
      padding: rem(2px) rem(8px) rem(10px);
    }
  }

  .myprize-list {
    width: 68vw;
    border-radius: 8px;
    padding: rem(30px) rem(12px) rem(12px);
    background-color: #fff;
    margin-top: rem(-20px);
    .title {
      font-size: 16px;
      font-weight: 600;
      text-align: center;
    }
    img {
      max-width: rem(90px);
      max-height: rem(90px);
    }
    .myprize {
      padding: rem(5px);
      width: rem(100px);
      border-radius: 16px;
      margin: 0 auto;
      background-color: #e2a941;
      color: #fff;
      margin-top: rem(6px);
    }
  }

  .checkPrize {
    position: absolute;
    right: 0;
    bottom: rem(-24px);
    margin-top: rem(20px);
    width: rem(60px);
    border-radius: rem(34px) 0 0 rem(34px);
    padding: rem(8px) rem(6px) rem(8px) rem(20px);
    background-color: #efe5e1;
    color: #2d8961;
    font-size: rem(14px);
    font-weight: 500;
  }

  .rules {
    width: 84vw;
    margin: rem(15px) auto 0;
    padding: rem(10px) rem(23px);
    background-color: #49c790;
    box-shadow: 1px 0px 31px inset #2d8961;
    border-radius: 12px;
    text-align: left;
    h3 {
      text-align: center;
      color: #2d8961;
      position: relative;
      font-weight: 500;
      margin-bottom: rem(5px);
    }

    p {
      color: white;
      line-height: 2;
      font-size: rem(12px);
    }
  }

  .pop-enter, .pop-leave-to {
    transform: translate3d(-50%, 0, 0) !important;
    opacity: 0;
  }

  .good-desc {
    color: #e75836;
    margin-top: 5vw;
    margin-bottom: rem(4px);
    font-size: 3.7vw;
  }

  .title {
    padding: 0 rem(35px);

    img {
      max-width: 100%;
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .tips {
    color: #2d8961;
    margin-top: rem(18px);
    text-align: center;
  }

  #prize .btn-app {
    background-color: #4db87f;
  }
  .turn {
    width: 88vw;
    height: 88vw;
    border-radius: 50%;
    margin: rem(14px) auto;
    position: relative;
    background: url(../assets/images/circleP.png) #e89070 center / cover no-repeat;
    box-shadow: 0px 0px 30px #297554
  }

  .gift {
    position: absolute;
    top: 0;
    left: 25%;
    width: 45vw;
    height: 45vw;
    padding: rem(15px);
    text-align: center;
    transform-origin: center bottom;
    img {
      max-width: rem(48px);
      max-height: rem(48px);
    }
  }

  .prize-wrap {
    letter-spacing: 1px;
    position: relative;
    background: url(../assets/images/turn_bg.png) center top/100% auto no-repeat, url(../assets/images/ye.png) left 8.5rem/15vw auto no-repeat;
  }

  .startBtn, .notStartBtn, .activityEnd {
    width: 26vw;
    height: 27.2vw;
    position: absolute;
    top: 50%;
    left: 50%;
    color: #fff;
    margin-top: -13.4vw;
    margin-left: -13vw;
    border-radius: 50%;
    background-color: #ff4f79;
    text-align: center;
    background: url(../assets/images/start_btn.png) center / cover no-repeat;
  }

  .startBtn:active, .startBtn:focus {
    opacity: 0.9;
    transform: translateY(rem(2px)) scale(.99);
  }
  .notStartBtn {
    background-image: url(../assets/images/not_start.png);
  }
  .activityEnd {
    background-image: url(../assets/images/activity_end.png);
  }
  @for $i from 1 through 6 {
    .rotate#{$i} {
      transform: rotate(180 * $i / 3 - 60 + 0deg);
    }
  }

</style>
