let ua = window.navigator.userAgent.toLowerCase()
let timeId = null
export function setPageTitle(title) {
  document.title = title
}

export function getQueryString(url, field) {
  var href = url ? url : window.location.href;
  var reg = new RegExp('[?&]' + field + '=([^&#]*)', 'i');
  var string = reg.exec(href);
  return string ? string[1] : null;
}

export function fmtDate(date, fmt) {
  // 简单粗暴一点
  let reg = /^\d{4}(-|[\u4e00-\u9fa5])\d{1,2}(-|[\u4e00-\u9fa5])\d{1,2}/
  if (reg.test(date)) {
    date = new Date(date.replace(/-|[\u4e00-\u9fa5]/g, '/'))
  }
  date = new Date(date)
  let o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 小时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    'S': date.getMilliseconds(), // 毫秒
    'T': ['日', '一', '二', '三', '四', '五', '六'][date.getDay()]
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}

export function isArray(arr) {
  return isType(arr, 'Array')
}

export function isObject(obj) {
  return isType(obj, 'Object')
}

export function isFunction(fn) {
  return isType(fn, 'Function')
}

export function isEmpty(obj) {
  if (!obj) {
    return true
  }
  if (isArray(obj) && obj.length === 0) {
    return true
  }
  if (isObject(obj) && Object.keys(obj).length === 0) {
    return true
  }
  return false
}

export function isType(obj, type) {
  return Object.prototype.toString.call(obj) === '[object ' + type + ']'
}

export function isWeixin() {
  if (ua.toLowerCase().match(/MicroMessenger/i) == "micromessenger") {
    return true;
  } else {
    return false;
  }
}

export function isAndroid() {   //判断是否android机子
  return ua.indexOf('android') > -1 || ua.indexOf('adr') > -1; //&& ua.indexOf("mobile");
}

export function isApple() {   //判断是否apple机子,前者判断有iPhone机子不能识别。
  return /(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent) || !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
}

export function wakeApp(androidUrl, appleUrl) {
  var href
  if (isWeixin()) {
    return
  }
  if (isAndroid()) {
    href = androidUrl// this.apiConfig.downloadUrl.android
  } else if (isApple()) {
    href = appleUrl // this.apiConfig.downloadUrl.ios
  }
  timeId = setTimeout(() => {
    if (href) {
      window.location.href = href
    }
  }, 2500)
  document.addEventListener('visibilitychange', () => {
    var isHidden = document.hidden
    if (isHidden) {
      clearTimeout(timeId)
    }
  })
}

export function fromApp() {
  return (/galaxyworld/).test(ua)
}

export function getCookie(name) {
  let reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)')
  let arr = document.cookie.match(reg)
  if (arr) {
    return unescape(arr[2])
  } else {
    return null
  }
}

export function setCookie(name, value, day = 30) {
  let exp = new Date()
  exp.setTime(exp.getTime() + day * 24 * 60 * 60 * 1000)
  document.cookie = name + '=' + escape(value) + ';expires=' + exp.toGMTString()
}
