<template>
  <div class="page">
    <div class="content">
      <div class="p15 bgc-white">
        <p class="mt10 td" v-html="fetchData.content"></p>
      </div>
    </div>
  </div>
</template>
<script>
  import * as utils from '@/common/utils.js'

  export default {
    name: 'about',
    data() {
      return {
        fetchData: {}
      }
    },
    created() {
      var zone_id = utils.getQueryString(window.location.href, 'id')
      if (zone_id !== null) {
        this.axios.get(this.apiConfig.aboutInfo, {
          params: {zone_id}
        }).then(res => {
          if (res.s !== 1) {
            this.$toast.fail(res.m)
            return
          }
          this.fetchData = res.d
        })
      } else {
        setTimeout(() => {
          this.$router.push({
            name: 'Index'
          })
        }, 1000)
      }
    }
  }
</script>
