import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home.vue'
import Index from '@/views/index.vue'
import ServiceNews from '@/views/service-news.vue'
import Activity from '@/views/activity.vue'
import Notice from '@/views/notice.vue'
import Banner from '@/views/banner.vue'
import About from '@/views/about.vue'
import Help from '@/views/help.vue'
import NotFound from '@/views/notfound.vue'
import AppDownload from '@/views/app-download.vue'
import Prize from '@/views/prize.vue'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '',
    name: 'Index',
    component: Index
  }, {
    path: '/',
    name: 'Home',
    component: Home
  }, {
    path: '*',
    component: NotFound,
    name: 'notfound'
  }, {
    path: '/service-news',
    name: 'ServiceNews',
    component: ServiceNews
  }, {
    path: '/about',
    name: 'About',
    component: About,
    meta: {
      pageTitle: '关于我们'
    }
  }, {
    path: '/help',
    name: 'Help',
    component: Help,
    meta: {
      pageTitle: '帮助'
    }
  }, {
    path: '/app-download',
    name: 'AppDownload',
    component: AppDownload,
    meta: {
      pageTitle: '下载APP'
    }
  }, {
    path: '/notice',
    name: 'Notice',
    component: Notice
  }, {
    path: '/banner',
    name: 'Banner',
    component: Banner,
    meta: {
      pageTitle: '详情'
    }
  }, {
    path: '/activity',
    name: 'Activity',
    component: Activity,
    meta: {
      pageTitle: '活动详情'
    }
  }, {
    path: '/prize',
    name: 'Prize',
    component: Prize,
    meta: {
      pageTitle: '抽奖活动详情'
    }
  }]
})
