<template>
  <div>
    <NewsContent :fetchObject="fetchObject"></NewsContent>
  </div>
</template>
<script>
  import NewsContent from '@/components/NewsContent.vue'
  import * as utils from '@/common/utils.js'

  export default {
    name: 'serviceNews',
    data() {
      return {
        fetchObject: {
          url: this.apiConfig.serviceContent,
          method: 'POST',
          data: {}
        }
      }
    },
    components: {
      NewsContent
    },
    created() {
      var information_id = utils.getQueryString(window.location.href, 'id')
      if (information_id !== null) {
        this.fetchObject.data = {information_id}
      } else {
        setTimeout(() => {
          this.$router.push({
            name: 'Index'
          })
        }, 1000)
      }
    }
  }
</script>
